<template>
   <div id="player">
        <!-- 全屏播放器 -->
        <div class="big-player" v-bind:class="{hide: isHide}">
            <div class="header">
                    <span class="iconfont icon-zuojiantou"  @click="toggerHide"></span>
                    <div>
                         <p class='name'>{{ this.$store.getters.getSong.songName +''}}</p>
                         <p class='info'>{{ this.$store.getters.getSong.singer[0].name +"   "+ this.$store.getters.getSong.albumname }}</p>
                    </div>
                    <!-- 我喜欢 -->
                    <span class="like iconfont " @click="addlike" v-bind:class="{ 'icon-xihuan1':!like, 'icon-xihuan':like}"></span>
            </div>
            <div class="cover" v-show="showCvoer"  @click="showCvoer = !showCvoer">
                <img :src=" this.$store.getters.getSong.coverUrl || './static/player.jpg' " alt="">
            </div>
            <div class="lrc" v-show="!showCvoer" @click="showCvoer = !showCvoer">
                <div class="lrc-box" v-bind:style="{ top: lrctop + 'px' }">
                    <p class="item" v-for="(texts,index) in lrc" v-bind:key="index" :class="{act:index == lrcCrrLin-1}">
                        {{ texts.text }}
                    </p>
                </div>
            </div>
            <div class="control-group">
                    <div class="progress-box">
                        <!-- 当前时间 -->
                        <span class="current-time">
                            {{ currentTime |timeFormat() }}
                        </span>
                        <!-- 进度条 -->
                        <div class="progress" >
                            <div class="current" v-bind:style="styleObject"></div>
                        </div>
                        <span class="total">
                            {{ totalTime | timeFormat() }}
                        </span>
                    </div>
                    <div class="btn-group">
                        <!-- 播放状态 -->
                         <span class="iconfont"
                          v-bind:class="{'icon-liebiaoxunhuan':(this.$store.getters.getPlayStatus == 1),
                          'icon-suijibofang':(this.$store.getters.getPlayStatus == 2),
                          'icon-danquxunhuan':(this.$store.getters.getPlayStatus == 3)
                          }"
                          @click="setPlayStatus"></span>
                         <!-- 上一曲 -->
                         <span class="iconfont icon-shang" @click="prev"></span>
                         <!-- 播放/暂停 -->
                         <span class="iconfont play" v-bind:class="{ 'icon-bofang': !this.$store.getters.getPlaying, 'icon-zanting': this.$store.getters.getPlaying}" @click.stop="setPlaying"></span>
                         <!-- 下一曲 -->
                         <span class="iconfont icon-shang1" @click="next"></span>
                         <!-- 打开当前播放列表 -->
                         <span class="iconfont icon-bofangliebiao" @click="showPanel"></span>
                    </div>
            </div>
            <play-list :visible.sync="visible"></play-list>
        </div>
        <!-- 迷你播放器 -->
        <div class="sm-player" v-bind:class="{hide:!isHide}" @click="toggerHide">
            <div class="sm-player-box">
                <div class="coverBox">
                    <img :src=" this.$store.getters.getSong.coverUrl || './static/player.jpg' " alt="">
                </div>
                <div class="music-info">
                    <p clsss='name'>{{ this.$store.getters.getSong.songName }}</p>
                    <p class='info'>{{ this.$store.getters.getSong.singer[0].name +"   "+this.$store.getters.getSong.albumname }}</p>
                </div>
                <div class="music-btn">
                    <span class="iconfont" v-bind:class="{ 'icon-bofang': !this.$store.getters.getPlaying, 'icon-zanting': this.$store.getters.getPlaying}" @click.stop="setPlaying"></span>
                </div>
            </div>
        </div>
        <audio ref="mudom" @canplay="getTotal" :loop="this.$store.getters.getPlayStatus == 3">
            <source :src="singUrl" type="audio/ogg">
            <source :src="singUrl" type="audio/mpeg">
        </audio>
    </div>
</template>
<script>
import playList from './playList';
export default {
    components:{
        'playList':playList
    },
    data(){
        return {
            visible:false,
            isHide:true,
            // 总时长
            totalTime:0,
            // 当前时间
            currentTime:0,
            styleObject:{
                width:'0%'
            },
            singUrl:'',
            like:false,
            lrc:[],
            lrctop:200,
            lrcCrrLin:'w',
            showCvoer:true
            
        }
    },
    methods:{
        // 面板控制
        // 播放器的显示
        toggerHide:function(){
            this.isHide=!this.isHide;
        },
        // 播放列表的显示
        showPanel:function(){
          this.visible = true  //显示
        },
// 播放按钮组
        // 上一曲
        prev:function(){
           let index = this.$store.getters.getIndexes;
           let playlist = this.$store.getters.getPlayList;
            if(index <= 0){
                index = playlist.length -1;
            }else{
                index--;
            }
            this.$store.commit("setIndexes",index);
            console.log(index);
            this.$store.commit("setSong",playlist[index]);
        },
        //下一曲
        next:function(){
            let index = this.$store.getters.getIndexes;
            let playlist = this.$store.getters.getPlayList;
            if(index == (playlist.length - 1)){
                index = 0;
            }else{
                index++;
            }
            console.log(playlist[1]);
            this.$store.commit("setIndexes",index);
            this.$store.commit("setSong",playlist[index]);
        },
        // 播放和暂停
        setPlaying:function(){
            //改变播放器的状态
            this.$store.commit("setPlaying");           
        },
        // 切换播放模式
        setPlayStatus:function(){
            let playStatus = this.$store.getters.getPlayStatus;
            if(playStatus==1){
                playStatus = 2;
            }else if(playStatus==2){
                playStatus = 3;
            }else if(playStatus==3){
                playStatus = 1;
            }
            this.$store.commit("setPlayStatus",playStatus);
        },
// 进度条相关
        //获取总时间
        getTotal:function(){
            this.totalTime=this.$refs.mudom.duration;
        },
        //获取当前时间,熟悉进度条和进度条上的时间
        updateTime:function(){
           this.currentTime=this.$refs.mudom.currentTime;//当前时间
           this.styleObject.width = ((this.currentTime/this.totalTime) * 100) + '%';//进度条 当前时间/总时间
           // 点前状态不为单曲循环时播放完歌曲播放下一曲
           if((this.$store.getters.getPlayStatus != 2) && this.currentTime == this.totalTime){
                this.next();
           }
        },
        //定时器,每个1秒钟更新一下时间
        timeOut:function(){
            this.clearTimeSet=setInterval(() => { 
                this.updateTime();
                if(this.lrc.length>1){
                    this.playLrc();
                }
                
            }, 500)
        },        
// 其他 
        // 播放
        palyMusic:function(){
            let audio=this.$refs.mudom;
            if( this.$store.getters.getPlaying ){
                // 播放音频,并开启定时器,更新进度条
                audio.play();
                // this.getlyric();
                this.timeOut();

            }else{
                // 暂停音乐并,关闭进度条,暂停定时器
                audio.pause();
                clearInterval(this.clearTimeSet);
            }
                  
        },
        // 播放歌词
        playLrc(){
           if(this.lrcCrrLin == this.lrc.length-1){
                this.lrctop = 200 - (this.lrcCrrLin*30);
           }else if(this.currentTime > this.lrc[this.lrcCrrLin].time && this.currentTime < this.lrc[this.lrcCrrLin+1].time){
                //歌词偏移
                this.lrctop = 200 - (this.lrcCrrLin*30);
                //单反歌词变色
                this.lrcCrrLin++;
           }
        },
        // 获取歌曲的地址
        getMusrcUrl(songmid){
            let guid='2916248128';
            let url = 'https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg'
            this.$jsonp(url,{
                format : 'json205361747',
                platform : 'yqq',
                cid: '205361747',
                songmid : songmid,
                filename : 'C400'+ songmid + '.m4a',
                guid: guid}
            ).then(json =>{
                this.singUrl='http://ws.stream.qqmusic.qq.com/'+json.data.items[0].filename+'?fromtag=0&guid='+guid+'&vkey='+json.data.items[0].vkey;
           }).catch(err =>{
                console.log('获取音乐信息失败----------');
            });
            
        },
        getlyric:function(id){
            let url='/api/getLyric';
            this.$axios.get(url,{
                params:{
                    g_tk:5381,
                    uin:0,
                    format:'json',
                    inCharset:'utf-8',
                    outCharset: 'utf-8',
                    notice: 0,
                    platform: 'h5',
                    needNewCode: 1,
                    nobase64: 1,
                    musicid: id,
                    songtype: 0,
                    "_": 1581229842877,
                    jsonpCallback:'jsonp1'
                }
            }).then(res =>{
                let lrc = res.data.lyric;
                if(lrc != null){
                    this.lrcCrrLin = 0;
                }
                this.lrc = this.parseLyric(lrc);                
            })       
        },
        //歌词处理,转换为数组对象
        parseLyric:function(text){
            var div = document.createElement('div');
            div.innerHTML = text;
            text = div.innerHTML;
            let arr = new Array();
            arr = text.split('\n');
            let lrc = new Array();
            for(let i=0;i<arr.length;i++){
                var d = arr[i].match(/\[\d{2}:\d{2}((\.|\:)\d{2})\]/g);  //正则匹配播放时间
                var t = arr[i].split(d); //以时间为分割点分割每行歌词，数组最后一个为歌词正文
                if(t[1] != null && t[1] !='') { //过滤掉空行等非歌词正文部分
                    //换算时间，保留两位小数
                    var dt = String(d).split(':');
                    var _t = parseInt(dt[0].split('[')[1])*60+parseFloat(dt[1].split(']')[0]);
                    lrc.push({time:_t, text:t[1]});    
                }
            }
            return lrc;
            
        },
        isLike(){
           let arr = this.$store.getters.getLikeList;
           this.like = false;
            for(let i = 0;i < arr.length ; i++){
                if(arr[i] == this.$store.getters.getSong){
                    this.like = true;
                }
            }
        },
        addlike(){
            if(!this.like){
                this.$store.commit('addSongToLikeList',this.$store.getters.getSong);
                console.log('我添加了')
            }else{
                this.$store.commit('removeSongToLikeList',this.$store.getters.getSong);
                console.log('我删除了')
            }
            this.like = !this.like;
            
        },

    },
    watch:{
        '$store.state.song'(){
            this.getMusrcUrl(this.$store.getters.getSong.songmid);
            this.getlyric(this.$store.getters.getSong.id)
        },
        '$store.state.playing'(){
            //当仓储中的状态发生改变是调用播放数将
            this.palyMusic();
        },
        'singUrl'(){
            let audio=this.$refs.mudom;
            //改变地址后重新加载音频
            audio.load();
            this.palyMusic();
            this.isLike();
            if( !this.$store.getters.getPlaying ){ 
               this.setPlaying(); 
            }
            
        }
    },
    filters:{
        // 时间处理
        // 将时间转换为分钟格式
        timeFormat:function(times){
            //获取分钟数
            let timeMin=parseInt(times/60);
            // 前面补0处理
            if(timeMin<10){
                timeMin='0'+timeMin;
            }
            let timeS=parseInt(times% 60)
            if(timeS<10){
                timeS='0'+timeS;
            }
            return timeMin + ':' + timeS;
        }
    }
    
}
</script>
<style scoped>
    .hide{
        display: none;
    }
    /* big-player */
    .big-player{
        position: fixed;
        z-index: 999;
        top:0;
        width:100%;
        height:100%;
        background-color:#2c323b;
    }
    .big-player .header{
        margin-top:25px;
        height:50px;
        display:flex;
        justify-content: space-between;
    }
    .big-player .header .icon-zuojiantou{
        text-align: center;
        line-height:50px;
        font-size:30px;
        height:50px;
        width:55px;
        color:#FF9900;
    }
    .big-player .header .like{
        text-align: center;
        line-height:50px;
        font-size:50px;
        height:50px;
        width:55px;
        color:#FF9900;
    }
    .big-player .header .icon-xihuan{
        font-size:33px;
        color:#FF9900;
    }
    .big-player .header .icon-xihuan1{
        font-size:35px;
        color:#FF9900;
    }
    .big-player .header .name{
        font-size:16px;
        color:#fff;
        font-weight:700;
        text-align: center
    }
    .big-player .header .info{
        margin-top:10px;
        font-size:14px;
        color:#999;
        font-weight:700;
        text-align: center
    }
    .big-player .cover{
        height:246px;
        width:246px;
        margin:76px auto 0;
    }
    .big-player .cover img{
        border-radius:123px;
    }
    .control-group{
        position: absolute;
        bottom:0;
        box-sizing: border-box;
        /* padding-top:15px; */
        height:120px;
        width:100%;
    }
    .progress-box{
        margin:15px 0;
        box-sizing: border-box;
        padding:0 10px;
        display: flex;
    }
    .current-time,
    .total{
        text-align: center;
        height:20px;
        line-height:20px;
        width:60px;
        font-size:12px;
        color:#fff;
    }
    .progress{
        margin-top:7px;
        height:5px;
        width:100%;
        background-color: #666;
    }
    .progress .current{
        height:5px;
        width:0%;
        background-color:#fff;
    }
    .btn-group{
        display: flex;
        justify-content: space-around; 
        align-items:center
    }
    .btn-group .iconfont{
        text-align: center;
        line-height:40px;
        font-size:35px;
        height:40px;
        width:40px;
        color:#f90;
    }
    .btn-group .play{
        font-size:45px;
        height:50px;
        width:50px;
    }
    .btn-group .icon-ERP_directory_classification{
        font-size:30px;
    }
    .btn-group .icon-suiji{
        font-size:28px;
    }
     /* big-player end */
     /* smplay */
    .sm-player{
        position: fixed;
        bottom:0;
        width:100%;
        height:60px;
        background-color:#2c323b;
    }
     .sm-player-box{
        display: flex;
    }
    .sm-player .coverBox{
        height:60px;
        width:60px;
    }
    .sm-player .coverBox img{
        padding:5px;
        height:50px;
        width:50px;
        border-radius:30px;
    }
    .sm-player .music-info{
        flex-grow:2;
        height:50px;
        padding:5px;
    }
    .sm-player .music-info p{
        font-size:16px;
        color:#fff;
    }
    .sm-player .music-info .info{
        margin-top: 10px;
        height:20px;
        line-height:20px;
        font-size:14px;
        color:#aaa;
    }
    .sm-player .music-btn{
        margin-top:10px;
        height:60px;
        width:60px;
    }
    .sm-player .music-btn .iconfont{
        height:60px;
        width:60px;
        font-size:35px;
        font-weight: 500;
        color:#FF9900;
    }
    /* 歌词 */
    .lrc{
        margin-top:30px;
        position: relative;
        height:400px;
        width:100%;
        overflow: hidden;
    }
    .lrc .lrc-box{
        position:absolute;
        width:100%;
        top:-30px;
    }
    .lrc p{
        height:30px;
        font-size:18px;
        line-height:30px;
        text-align: center;
        color:#666;
    }
    .lrc .act{
        color:#fff;
    }
</style>



