<template>
    <div class="songlist" v-show="visible" @click.self="hideVisible">
        <div class="songlistbox">
            <div class="herder">
                <span @click="setPlayStatus">
                    <span class="iconfont"
                          v-bind:class="{'icon-liebiaoxunhuan':(this.$store.getters.getPlayStatus == 1),
                          'icon-suijibofang':(this.$store.getters.getPlayStatus == 2),
                          'icon-danquxunhuan':(this.$store.getters.getPlayStatus == 3)
                          }"></span>
                    {{ this.$store.getters.getPlayStatus == 1 ? '顺序播放' :  this.$store.getters.getPlayStatus == 2 ? '随机播放':'单曲循环'}}
                    ({{ $store.getters.getSequenceList.length }})
                </span>
            </div>
            <div class="list">
                <ul>
                        <li class="item" v-for="(song,index) in $store.getters.getSequenceList" v-bind:key="index" @click="play(song)">
                            <span class="song-name">{{ song.songName }}</span>
                            <span class="sing-name">{{ song.singer[0].name }}</span>
                        </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
export default{
    data(){
        return{
            statusText:'',
        }
    },
    props:{
        visible:{
            type:Boolean,
            default:false
        }
    },
    methods:{
        hideVisible:function(){
            this.$emit('update:visible',false);
        },
        play:function(song){
            this.$store.commit("setSong",song)         
        },
        setPlayStatus:function(){
            let playStatus = this.$store.getters.getPlayStatus;
            if(playStatus==1){
                playStatus = 2;
            }else if(playStatus==2){
                playStatus = 3;
            }else if(playStatus==3){
                playStatus = 1;
            }
            this.$store.commit("setPlayStatus",playStatus);
        }
    }
}
</script>
<style scoped>
.songlist{
    position:fixed;
    bottom:0;
    height:100%;
    width:100%;
}
.songlist .songlistbox{
    position: fixed;
    bottom:0;
    height:400px;
    width:100%;
}
.herder{
    height:54px;
    box-sizing:border-box;
    padding-left:20px;
    line-height:54px;
    border-radius:27px 27px 0 0;
    border-bottom:1px solid #999;
    background-color:#fff;
}
.herder .iconfont{
    display: inline-block;
    width:30px;
    font-size:20px;
}
.herder .icon-suiji{
    font-size:18px;
}
.list{
    background-color:#fff;
    height:345px;
    overflow-y:auto;
}
.songlist .list .item{
    padding:0 10px;
    height:45px;
    line-height:45px;
  
}
.songlist .list .song-name{
    font-size:18px;
}
.songlist .list .sing-name{
    font-size:12px;
    color:#666;
}
</style>


