<template>
  <div id="herder">
      <ul>
            <li >
                <router-link to="/recommend">推荐</router-link>     
            </li>
            <li>
                <router-link to="/search">搜索</router-link>     
            </li>
            <li>
                <router-link to="/singer">歌手</router-link>     
            </li>
            <li>
                <router-link to="/user">我的</router-link>     
            </li>
      </ul>
  </div>
</template>

<script>
export default {
  name: 'Herder'
}
</script>

<style scoped>
 #herder{
     z-index:99;
        position:fixed;
        top:0;
        width:100%;
        height:50px;
        background-color:#2c323b;
        box-shadow:-2px 6px 10px 0 #bfbfbf;
    }
#herder ul{
        display: flex;
        justify-content: space-around;
        align-content: center;
}
#herder ul li a{
    display:block;
    height:50px;
    padding:0 20px;
    line-height:50px;
}
/* 路由选中时的样式 */
.selection{
        box-sizing: border-box;
        font-weight:bold;
        color:#F90;
        border-bottom:2px solid #f90
}
</style>



