<template>
    <v-touch v-on:swipeleft="swiperleft" v-on:swiperight="swiperright" class="wrapper">
    <div class="search">
        <div class="search-from">
            <input type="text" v-model="testmsg" @change="search" @focus="clean" name="" id="">
        </div>
        <div class="musiclist">
            <ul>
                <li v-for="(music,index) in musiclist" @click="addmusic(music)" v-bind:key='index'>
                    <span class="music-name">{{ music.songname }}</span>
                    <span class="info">{{ music.singer[0].name }} - {{ music.albumname}} </span>
                </li>
            </ul>
        </div>
    </div>
    </v-touch>
</template>
<script>
export default {
    data: function () {
        return {
            testmsg:'',
            musiclist:[],
            page:1
        }
     },
     methods:{
         swiperleft(){
            this.$router.push({'path':'/singer'});
        },
        swiperright(){
             this.$router.push({'path':'/recommend'});
        },

         clean:function(){
             this.testmsg=''
             this.musiclist=[];
         },
         //使用jsonp搜索
         search: function(){
            let url='https://c.y.qq.com/soso/fcgi-bin/client_search_cp?aggr=1&cr=1&flag_qc=0&n=30';
            this.$jsonp(url, { w: this.testmsg,p:this.page, callbackName: "callback"}).then(json => {
                // 干掉收费的音乐
                for(let i=json.data.song.list.length-1;i>=0;i--){
                    if(json.data.song.list[i].pay.payplay==1){
                        json.data.song.list.splice(i, 1);
                    }
                }
                this.musiclist=this.musiclist.concat(json.data.song.list);
            }).catch(err => {
                console.log('搜索失败');
            })
         },
        addmusic(music){
            let song = {
                id:music.songid,
                songmid:music.songmid,
                // 歌曲名称
                songName:music.songname,
                //歌手
                singer:music.singer,
                // 封面地址
                coverUrl:'http://imgcache.qq.com/music/photo/album_300/'+(music.albumid)%100+"/300_albumpic_"+music.albumid+"_0.jpg",
                // 专辑
                albummid:music.albummid,
                // 专辑名称
                albumname:music.albumname,
            }
            this.$store.commit("addSongToSequenceList",song);
            this.$store.commit("setSong",song) 
        },
        // 下拉加载功能
        scroll(person) {
            window.onscroll = () => {
	            var scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);
	            //滚动条滚动距离
	            var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
	            //窗口可视范围高度
	            var clientHeight = window.innerHeight || Math.min(document.documentElement.clientHeight, document.body.clientHeight);
	            if (clientHeight + scrollTop >= scrollHeight) {
                    this.page ++;
                    this.search();
                }
            }
        }
    },
    mounted() {
        this.scroll(this.person);
     }
}
</script>
<style scoped>
    .wrapper{
        width:100%;
        height:500px;
        touch-action: pan-y!important;
    }
    .search-from{
        box-sizing: border-box;
        position: fixed;
        top:70px;
        padding:8px 10px;
        width:100%;
        background-color:#aac;
    }
    .search-from input[type='text']{
        box-sizing: border-box;
        padding-left:15px;
        margin:auto;
        height:30px;
        width:100%;
        border-radius:5px;
    }
    .musiclist{
        margin-top:60px;
    }
    .musiclist ul{
        background-color:#fff;
        margin-bottom:60px;
        padding:0 10px;
    }
    .musiclist li{
        height:50px;
        box-sizing: border-box;
        padding:7px 0;
        border-bottom:1px solid #ddd;
    }
    .musiclist li .music-name{
        display:block;
        height:20px;
        width:100%;
        overflow: hidden;
        line-height:20px;
        font-size:16px;

    }
    .musiclist li .info{
        display:block;
        height:16px;
        width:100%;
        overflow: hidden;
        line-height:16px;
        font-size:14px;
        color:#666
    }
</style>



