import Vue from 'vue';
import Vuex from 'vuex';
import shuffle from '../tool/shuffle';
Vue.use(Vuex);

const state={
    // 当前歌曲
    song:{
        id:'',
        // 歌曲id
        songmid:'',
        // 歌曲名称
        songName:'当前无歌曲',
        //歌手
        singer:[{
            name:''
        }],
        // 封面地址
        coverUrl:'',
        // 专辑id
        albummid:'',
        // 专辑名称
        albumname:'',
    },
    // 播放列表状态 1 列表循环 2 随机播放 3 单曲循环
    playStatus:1,
    // 播放状态
    playing:false,
    // 当前播放列表
    PlayList:[],
    //顺序列表
    SequenceList:[],
    // 索引
    indexes:-1,
    // 用户喜欢的列表
    likeList:[]
}

// 用于获取数据
const getters={ 
    getSong(state){
        return state.song;
    },
    getPlayStatus(state){
        return state.playStatus;
    },
    getPlaying(state){
        return state.playing;
    },
    getPlayList(state){
        if(state.PlayList.length==0){
            state.PlayList = state.SequenceList;
        }
        return state.PlayList;
    },
    getSequenceList(state){
        return state.SequenceList;
    },
    getIndexes(state){
        return state.indexes;
    },
    getLikeList(state){
        return state.likeList;
    }
}
// 用于改变数据
const mutations={
    setSong(state,song){
        state.song = song;
    },
    setPlayStatus(state,PlayStatus){
        state.playStatus = PlayStatus;
        if(state.playStatus == 2){
            state.PlayList = shuffle(state.SequenceList);
        }else{
            state.PlayList = state.SequenceList
        }
    },
    setPlaying(state){
       state.playing = !state.playing;
    },
    setSequenceList(state,arr){
        state.SequenceList = arr;
    },
    // 添加歌曲
    addSongToSequenceList(state,song){
        for(let i=0;i<state.SequenceList.length;i++){
            if(song.songmid == state.SequenceList[i].songmid){
                return '';
            }
        }
        // state.SequenceList = state.SequenceList.concat(song);
        state.SequenceList.unshift(song);
        localStorage.setItem("sequenceList",JSON.stringify(state.SequenceList));
        
    },
    // 改变索引
    setIndexes(state,indexes){
        state.indexes = indexes;
    },
    setLikeList(state,arr){
        state.likeList = arr;
    },
    addSongToLikeList(state,song){
        state.likeList = state.likeList.concat(song);
        localStorage.setItem("likeList",JSON.stringify(state.likeList));
    },
    removeSongToLikeList(state,song){
        for(let i=0;i<state.likeList.length;i++){
            if(song == state.likeList[i]){
                state.likeList.splice(i,1);
            }
        }
        localStorage.setItem("likeList",JSON.stringify(state.likeList));
    }
}
export default new Vuex.Store({
    state,
    getters,
    mutations
});


