<template>
  <v-touch v-on:swiperight="swiperright" class="wrapper">
	  <div id="user">
        <div class="list">
            <ul>

              <li @click="goLikeList">
                <span class="iconfont icon-xihuan"></span>
                <span>我喜欢</span>
              </li>

              <li @click="goHistory">
                 <span class="iconfont icon-shijianlishijilujishizhongbiaomianxingx"></span>
                
                <span>播放历史</span>
              </li>
            </ul>

        </div>
    </div>
  </v-touch>
</template>

<script>
export default {
  methods:{
    swiperright: function () {
      this.$router.push({'path':'/singer'});
    },
    goLikeList(){
        this.$router.push({
        path:'/user/likelist'
      })
    },
    goHistory(){
      this.$router.push({
        path:'/user/history'
      })
    }

  }
}
</script>
<style scoped>
.wrapper{
  width:100%;
  height:500px;
  touch-action: pan-y!important;
}
#user{
  margin-top:10px;
}
.herder{
  padding:0 10px;
  display: flex;
  justify-content: space-between;
}
.herder button{
  height:30px;
  width:30px;
}
.list{
  margin-top:10px;
  padding:0 10px;
}
.list li{
  display: flex;
  height:50px;
  margin-top:10px;
  background-color:#eee;
}
.list li .iconfont{
  height:50px;
  width:50px;
  font-size:30px;
  color:#f90;
}
.list li span{
  padding-left:10px;
  height:50px;
  line-height:50px
}
</style>



