<template>
  <div id="app">
      <herder></herder>
       <router-view></router-view>
      <player></player>
  </div>
</template>

<script>
import herder from './components/common/herder.vue';
import player from './components/player/player.vue';
export default {
  name: 'App',
  components:{
      'herder':herder,
      'player':player
  },
  methods:{
    setSequenceList(){
      let str=localStorage.getItem("sequenceList");
      let arr =JSON.parse(str) || []
      this.$store.commit('setSequenceList',arr);
    },
    setLikeList(){
      let str=localStorage.getItem("likeList");
      let arr =JSON.parse(str) || []
      this.$store.commit('setLikeList',arr);
    }
  },
  created(){
    this.setLikeList();
    this.setSequenceList();
  }
}
</script>

<style>
#app {
  background-color:#fff;
  box-sizing: border-box;
  padding-top:60px;
  padding-bottom:66px;
}
</style>



