<template>
  <v-touch v-on:swipeleft="swiperleft" v-on:swiperight="swiperright" class="wrapper">
  <div id="singer">
          <div class="herder">
              <ul>
                  <li @click="setTagesArea(-100)" :class="{act:(this.stdata.singerList.param.area==(-100))}">全部</li>
                  <li @click="setTagesArea(200)" :class="{act:(this.stdata.singerList.param.area==(200))}">内地</li>
                  <li @click="setTagesArea(2)" :class="{act:(this.stdata.singerList.param.area==(2))}">港台</li>
                  <li @click="setTagesArea(5)" :class="{act:(this.stdata.singerList.param.area==(5))}">欧美</li>
                  <li @click="setTagesArea(4)" :class="{act:(this.stdata.singerList.param.area==(4))}">日本</li>
                  <li @click="setTagesArea(3)" :class="{act:(this.stdata.singerList.param.area==(3))}">韩国</li>
                  <li @click="setTagesArea(6)" :class="{act:(this.stdata.singerList.param.area==(6))}">其他</li>
              </ul>
            
            
              <ul>
                  <li @click="setTagersSex(-100)" :class="{act:(this.stdata.singerList.param.sex==(-100))}">全部</li>
                  <li @click="setTagersSex(0)" :class="{act:(this.stdata.singerList.param.sex==(0))}">男</li>
                  <li @click="setTagersSex(1)" :class="{act:(this.stdata.singerList.param.sex==(1))}">女</li>
                  <li @click="setTagersSex(2)" :class="{act:(this.stdata.singerList.param.sex==(2))}">组合</li>
              </ul>
            
              <ul>
                  <li @click="setTagesGenre(-100)" :class="{act:(this.stdata.singerList.param.genre==(-100))}">全部</li>
                  <li @click="setTagesGenre(1)" :class="{act:(this.stdata.singerList.param.genre==(1))}">流行</li>
                  <li @click="setTagesGenre(6)" :class="{act:(this.stdata.singerList.param.genre==(6))}">嘻哈</li>
                  <li @click="setTagesGenre(2)" :class="{act:(this.stdata.singerList.param.genre==(2))}">摇滚</li>
                  <li @click="setTagesGenre(4)" :class="{act:(this.stdata.singerList.param.genre==(4))}">电子</li>
                  <li @click="setTagesGenre(3)" :class="{act:(this.stdata.singerList.param.genre==(3))}">民谣</li>
                  <li @click="setTagesGenre(8)" :class="{act:(this.stdata.singerList.param.genre==(8))}">R&b</li>
                  <li @click="setTagesGenre(10)" :class="{act:(this.stdata.singerList.param.genre==(10))}">民歌</li>
                  <li @click="setTagesGenre(9)" :class="{act:(this.stdata.singerList.param.genre==(9))}">轻音乐</li>
                  <li @click="setTagesGenre(5)" :class="{act:(this.stdata.singerList.param.genre==(5))}">爵士</li>
                  <li @click="setTagesGenre(14)" :class="{act:(this.stdata.singerList.param.genre==(14))}">古典</li>
                  <li @click="setTagesGenre(25)" :class="{act:(this.stdata.singerList.param.genre==(25))}">乡村</li>
                  <li @click="setTagesGenre(20)" :class="{act:(this.stdata.singerList.param.genre==(20))}">蓝调</li>
              </ul>
          </div>
          <div class="list">
              <ul>
                  <!-- <router-link to='/singer/singerDetails'> -->
                    <li v-for="item in singerList" :key='item.singer_id' @click="goSingerDetails(item)">
                      <img :src="item.singer_pic" alt="">
                      <span>{{ item.singer_name }}</span>
                    </li>
                  <!-- </router-link> -->
              </ul>
          </div>
          
  </div>
  </v-touch>
</template>

<script>
export default {
  data(){
    return{
      singerList:[],
      stdata:{
        comm:{ct:0,cv:0},
        singerList:{
          module:"Music.SingerListServer",
          method:"get_singer_list",
          param:{area:-100,sex:-100,genre:-100,index:-100,sin:0,cur_page:1}
        }
      }
    }
  },
  methods:{
    // 切换目录
    swiperleft(){
      this.$router.push({'path':'/user'});
    },
    swiperright(){
      this.$router.push({'path':'/search'});
    },


    goSingerDetails(item){
      this.$router.push({
        path:'/singer/singerDetails',
        name:'singerDetails',
        params:{
          ObjSinger:item
        }
      })
    },
    //切换类型
    setTagesArea(id){
        this.stdata.singerList.param.area = id;
        this.singerList = [];
        this.getSingerList();
    },
    setTagersSex(id){
        this.stdata.singerList.param.sex = id;
        this.singerList = [];
        this.getSingerList();
    },
    setTagesGenre(id){
      this.stdata.singerList.param.genre = id;
      this.singerList = [];
      this.getSingerList();
    },
    //数据
    getSingerList(){
      let url='https://u.y.qq.com/cgi-bin/musicu.fcg'
          this.$jsonp(url,{
            '-':'getUCGI4903206354262617',
            g_tk: 5381,
            loginUin: 0,
            hostUin: 0,
            format: 'json',
            inCharset: 'utf8',
            outCharset: 'utf-8',
            notice: 0,
            platform: 'yqq.json',
            needNewCode: 2,
            data:JSON.stringify(this.stdata)
          }).then(json => {
              this.singerList = this.singerList.concat(json.singerList.data.singerlist);
            }).catch(err => {
                console.log('获取失败');
                console.log(err)
          })
    },
    //下拉加载
    scroll(person) {
     window.onscroll = () => {
          var scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);
	            //滚动条滚动距离
	        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
	            //窗口可视范围高度
	        var clientHeight = window.innerHeight || Math.min(document.documentElement.clientHeight, document.body.clientHeight);
	        if (clientHeight + scrollTop >= scrollHeight) {
          this.stdata.singerList.param.cur_page++;
          this.stdata.singerList.param.sin = (this.stdata.singerList.param.cur_page-1) * 80;
          this.getSingerList();
          }
    };
  }
  },
  mounted(){
    this.getSingerList(),
    this.scroll(this.person)
  },  
}
</script>

<style scoped>
.wrapper{
     touch-action: pan-y!important;
}
#singer .list{
  margin:20px
}
#singer .herder{
  padding:5px 20px;
}
#singer .herder ul{
  height:40px;
  overflow-x: auto;
  white-space: nowrap;
}
#singer .herder ul::-webkit-scrollbar {
    display: none;
}
#singer .herder ul li{
  display: inline-block;
  margin:5px;
  height:30px;
  padding:0 10px;
  line-height:30px;
   border:1px solid #f90;
  border-radius:20px;
  font-size:14px;
}
#singer .herder ul .act{
  background-color:#e80;
  color:#fff;
}
#singer .right{
  position: fixed;
  top:70px;
  right:3px;
  width:16px;
}
#singer .right li{
  margin-top:2px;
  height:16px;
  width:16px;
  font-size:12px;
  color:#ccc;
  line-height:12px;
  text-align: center;
  background-color:#666;
}
#singer .list li{
  margin:10px 0;
  height:50px;
  line-height:50px;
  display: flex;
}
#singer .list li img{
  height:50px;
  width:50px;
  border-radius:25px;
}
#singer .list li span{
  margin-left:10px;
  font-size:16px;
}
</style>>



