<template>
  <v-touch v-on:swipeleft="swiperleft" class="wrapper">
  <div id="recommend">
      <div class="clause1">
          <h1 class="red">官方歌单</h1>
          <ul>
            <li v-for="item in OfficialSongList" :key="item.id" @click="goSonglist(item.id)">
                <img :src="item.cover" alt="">
                <p>{{ item.title }}</p>
            </li>
          </ul>
      </div>
      <div class="clause1">
          <h1 class="orange">达人歌单</h1>
          <ul>
            <li v-for="item in TalentSongList" :key="item.id" @click="goSonglist(item.id)">
                <img :src="item.cover" alt="">
                <p>{{ item.title }}</p>
            </li>
          </ul>
      </div>       
  </div>
  </v-touch>
</template>

<script>
export default {
  data(){
    return {
        OfficialSongList:[],
        TalentSongList:[],

    }
  },
  methods:{
    swiperleft(){
            this.$router.push({'path':'/search'});
    },
    getSongList(){
          let url='https://u.y.qq.com/cgi-bin/musicu.fcg'
          this.$jsonp(url,{
            cgiKey:'GetHomePage',
            data:'{"comm":{"g_tk":5381,"uin":"","format":"json","inCharset":"utf-8","outCharset":"utf-8","notice":0,"platform":"h5","needNewCode":1},"MusicHallHomePage":{"module":"music.musicHall.MusicHallPlatform","method":"MobileWebHome","param":{"ShelfId":[101,102,161]}},"hotkey":{"module":"tencent_musicsoso_hotkey.HotkeyService","method":"GetHotkeyForQQMusicMobile","param":{"remoteplace":"txt.miniapp.wxada7aab80ba27074","searchid":"1559616839293"}}}'
            }).then(json => {
              this.OfficialSongList =  json.MusicHallHomePage.data.v_shelf[0].v_niche[0].v_card;
              this.TalentSongList =   json.MusicHallHomePage.data.v_shelf[1].v_niche[0].v_card;     
            }).catch(err => {
                console.log('获取失败');
          })
    },
    goSonglist(id){
      this.$router.push({
        path:'/recommend/songlist',
        query:{
          id:id
        }
      })
    }
  },
  mounted(){
    this.getSongList()
  }
}
</script>

<style scoped>
  .wrapper{
     touch-action: pan-y!important;
  }
  .clause1 h1{
    margin:10px 0;
    padding:10px 0;
    color:#fff;
    text-align: center;
  }
  .clause1 ul{
    box-sizing:border-box;
    padding:0 10px;
     display: flex;
     flex-wrap: wrap;
     justify-content:space-between;
  }
  .clause1 li{
    width:30%;
    margin-bottom:20px;
  }
  .clause1 li img{
    border-radius:10px;    
  }
  .clause1 li p{
    color:#666;
    font-size:14px;
  }
  .red{
    background-color:#f00;
  }
  .orange{
    background-color:#f60;
  }
</style>>


