import Vue from 'vue'

import Router from 'vue-router'
Vue.use(Router)

import recommend from '@/components/recommend/recommend';
import songlist from '../components/recommend/songList.vue';

import search from '../components/search/search';

import singer from '../components/singer/singer';
import singerDetails from '../components/singer/singerDetails.vue'

import user from '../components/user/user';
import likelist from '../components/user/likelist';
import history from '../components/user/history'

export default new Router({
  linkActiveClass:'selection',
  routes: [
    {
      path: '/',
      component:recommend
    },
    {
      path: '/recommend',
      component: recommend
    },{
      path:'/recommend/songlist',
      component:songlist,
    }
    ,{
      path: '/search',
      component: search
    },{
        path: '/singer',
        component: singer
    },{
      path: '/singer/singerDetails',
      name:'singerDetails',
      component: singerDetails
    },
    {
      path: '/user',
      component: user
    },{
      path:'/user/likelist',
      component:likelist,
    },{
      path:'/user/history',
      component:history,
    }
    
  ]
})



