<template>
  <div id="songlist">
      <div class="herder">
          <img :src="herder.cover" alt="">
      </div>
      <div class="box">
             <p>{{ herder.title }}</p>
             <p v-html="herder.info"></p>
        </div>
      <div class="listbox">
          <ul>
              <li v-for="item in songlist" :key="item.id" @click="addmuisc(item)">
                  <p class="name">{{ item.name }}</p>
                  <p class="info">{{ item.singer[0].name }}.{{ item.album.name }}</p>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            herder:{
                cover:'',
                title:'',
                info:''
            },
            songlist:[]
        }
    },    
    methods:{
        addmuisc(music){
            let song = {
                id:music.id,
                songmid:music.mid,
                // 歌曲名称
                songName:music.name,
                //歌手
                singer:music.singer,
                // 封面地址
                coverUrl:this.herder.cover,
                // 专辑
                albummid:music.album.mid,
                // 专辑名称
                albumname:music.name,
            }
            this.$store.commit("addSongToSequenceList",song);
            this.$store.commit("setSong",song)
            //改变索引指向最后一个
            // this.$store.commit('footIndexes'); 
        },
        getList(){ 
        let url='/api/getDiscList';
        this.$axios.get(url,{
            params:{
                type: 1,
                json: 1,
                utf8: 1,
                onlysong: 0,
                new_format: 1,
                disstid: this.$route.query.id,
                g_tk: 5381,
                loginUin: 0,
                hostUin: 0,
                format: 'json',
                inCharset: 'utf8',
                outCharset: 'utf-8',
                notice: '0',
                platform: 'yqq.json',
                needNewCode: '0'
            }
        }).then(res =>{
            this.herder.cover = res.data.cdlist[0].dir_pic_url2 || res.data.cdlist[0].logo;
            this.herder.title = res.data.cdlist[0].dissname;
            this.herder.info = res.data.cdlist[0].desc;
            // 干掉收费的
            for(let i=res.data.cdlist[0].songlist.length-1;i>=0;i--){
                if(res.data.cdlist[0].songlist[i].pay.pay_play==1){
                    res.data.cdlist[0].songlist.splice(i, 1);
                }
            }
            this.songlist =  res.data.cdlist[0].songlist;      
        })
      }
  },
  mounted(){
      this.getList()
  }

}
</script>

<style scoped>
    .herder{
        margin-top:10px;
        position: relative;
        height:237px;
    }
    .herder img{
        position:absolute;
        top:0;
        left:0;
        height:100%;
        width:100%;
        object-fit: cover
    }
    .box{
       position:relative;
       top:-20px; 
       width:90%; 
       left:5%;
       background-color:#fff;
        border:1px solid #ccc;
        padding:10px;
        font-size:14px;
        color:#333;
        box-sizing: border-box;
        border-radius:10px;
    }
    .listbox li{
        padding:10px;
        padding-left:15px;
        border-bottom:1px solid #ddd;       
    }
    .listbox li p{
        /* background-color:#f0f; */
        color:#222;
    }
    .listbox li .neme{
        font-size:14px;
    }
    .listbox li .info{
        margin-top:4px;
        font-size:12px;
    }
</style>



