<template>
  <div id="">
     <div class="title">播放历史</div>
      <div class="list">
            <ul>
               <li class="item" v-for="song in this.$store.getters.getSequenceList" v-bind:key="song.id" @click="play(song)">
                <p class="name">{{ song.songName }}</p>
                <p class="info">{{ song.singer[0].name }} . {{ song.albumname }}</p>
                </li>
            </ul>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return {

        }
           
    },
    methods:{
        play(song){
            this.$store.commit("setSong",song)
        }
    }
      
}
</script>

<style scoped>
.title{
   margin-top:10px;
   height:40px;
   width:100%;
   font-size:16px;
   font-weight:700;
   text-align: center;
}
.list li{
     height:60px;
     border-top:1px solid #ccc;
     box-sizing:border-box;
     padding:10px;
 }
 .list .name{
     height:20px;
 }
 .list .info{
     height:20px;
     line-height:20px;
     font-size:12px;
     color:#666
 }
</style>>



